.game-info {
  display: flex;
  justify-content: center;
  font-size: 1.2em;
  margin: 10px 0;

  .info-item {
    width: 300px;
    display: flex;
    justify-content: center;

    label {
      font-weight: bold;
      color: #00648e;
    }
    
    strong {
      color: #e87722;
    }
  }
}



